.header {
  width: 100%;
  position: fixed;
  background-color: rgba(12, 7, 4, 0.7);
  color: #FFF;
  text-align: center;
  &__button {
    margin-top: 18px;
    @include css-lock(13, 16, 480, 960);
    // width: 179px
 } }    // height: 40px

.logo {
  margin: 15px 0 17px;
  float: left;
  text-align: left;
  line-height: 24px;
  &__title {
    @include H-l;
    @include css-lock(14, 20, 480, 960);
 }    // font-size: em(20)
  &__name {
    @include H;
    @include css-lock(16, 22, 480, 960);
    // font-size: em(22)
    font-weight: 700;
    text-transform: uppercase; } }
.telephone {
  margin-top: 14px;
  text-align: left;
  float: right;
  @include H-l;
  @include css-lock(10, 13, 480, 960);
  // font-size: em(13)

  &__title {
    opacity: 0.86;
    line-height: 1.8em; }
  &__number {
    @include H;
    @include css-lock(20, 28, 480, 960);
    // font-size: em(28)

    font-weight: 700;
    line-height: em(30, 28); } }
